import * as React from 'react';
import { Link } from 'react-router-dom';
import { IAppUser } from '../../models/app-user';
import { IUser } from '../../models/user/user';
import { isNN } from '../../util/index';
import { NAV } from '../../constants/nav';
import { COLOR } from '../../constants';

interface IProps {
  appUser: IAppUser,
  user: IUser,
  isExpand: boolean,
  onToggleMenu: () => void,
};

interface IState{
  activeMenu: string,
  activeTopMenu: string
};

export default class NavBar extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      activeMenu: '',
      activeTopMenu: ''
    };
    this.loadNav = this.loadNav.bind(this);
  }
  
  componentWillUnmount() {};
  
  loadNav = (nav: any, pTitle: string, isTopLevel: boolean = false) => {
    let that = this;
    return(
      <ul className={'navbar-nav' + ((!isTopLevel && this.state.activeTopMenu !== pTitle) ? ' hide' : '')} style={!isTopLevel ? styles.inner : {}}>
      {
        nav.map((d: any, i: number) => {
          if((isNN(d.path) || isNN(d.child)) && (!isNN(d.roles) || d.roles.indexOf(this.props.appUser.role) >= 0) && (!d.isMRole || this.props.user.role.split(',').length > 0)){
          return(
            <li className='nav-item'>
              <Link className='nav-link' to={isNN(d.path) ? d.path : '#' } onClick={() => {
                if(this.state.activeTopMenu === d.title){
                  this.setState({
                    activeTopMenu: ''
                  });
                }else if(isTopLevel){
                  this.setState({
                    activeTopMenu: d.title
                  });
                }
                this.setState({
                  activeMenu: d.title
                });
                if(isNN(d.path)){
                  this.props.onToggleMenu();
                }
              }}>{d.title}</Link>
              {
                isNN(d.child) && 
                that.loadNav(d.child, d.title)
              }
            </li>
          )
          }else{
            return null;
          }
        })
      }
      </ul>
    );
  };

  render() {
    return (
      <nav className={this.props.isExpand ? '' : 'collapse'} style={styles.nav}>
        {
          this.loadNav(NAV, '', true)
        }
      </nav>
    );
  }
};

const styles = {
  nav: {
    backgroundColor: COLOR.SECONDARY,
    color: COLOR.INVERSE
  },
  inner:{
    backgroundColor: COLOR.PRIMARY,
    paddingLeft: 10
  }
};